.mainScreen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


h1 {
    margin: 0;
    padding: 0;
    padding: 2%;
    font-size: 1.6rem;
}

h2 {
    margin: 0;
    padding: 0;
    font-size: 1.1rem;
}

.title {
    font-family: 'CasinoTitle';
    font-size: 3rem;
    color: rgb(109, 187, 95);

}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30px
}


.header {
    display: flex;
    background-color: rgb(109, 187, 95);
    width: 80%;
    height: 70px;
    justify-content: center;
}

.headerLogo {

    height: 100%;


    animation: spin 25s linear infinite;
    animation-fill-mode: forwards;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.headerLogo:hover {
    scale: 1.2;
    cursor: pointer;
}

.minus {
    font-size: 3rem;
}

.plus {
    font-size: 3rem;
}

.header div {
    display: inline;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    margin-inline: 1%;

}

.walletStats {
    color: white;
    font-size: 1rem;
    margin: auto;
    display: flex;
    align-items: center;
    background-color: rgb(0, 0, 0);
    width: 80%;



}

.walletStats p {
    margin-inline: 20px;
    margin: auto;
    padding: 10px;

}


.createGame:hover {
    background-color: rgb(255, 255, 144);
    cursor: pointer;
    scale: 1.2;
}

.betInputContainer {
    display: flex;
}

.betInputBox {
    width: 80px;

    margin: 8px;
    font-size: large;
    text-align: center;
    box-sizing: border-box;

}

input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;

}
.arrow {

    transition: transform 0.5s ease-in-out;
}
.arrow:hover{
    scale:1.2;
    cursor: pointer;
    transform: translateY(-10px);
    animation: bounce 0.5s infinite;
    cursor: pointer;
}

@keyframes bounce {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-5px);
    }
    100% {
      transform: translateY(0);
    }
  }
  
  #bounce {
    animation-duration: 0.5s;
    animation-fill-mode: both;
  }
  
  #bounce:active {
    animation-name: bounce;
  }
.gameGrid {
    display: grid;
    list-style: none;
    padding: 0;
    margin: 30px;
    
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(auto-fill, minmax(0px, auto));
    grid-gap: 10px;
    justify-content: center;

}

.gameGrid .game {
    margin: 0;
    padding: 0;
}

.myGames {

}

.openGames {
   
}


.openGames .gameGrid li:hover {
    cursor: pointer;
    scale: 1.2;
    background-color: rgb(109, 187, 95);

}

.myGames .gameGrid li:hover {
    cursor: pointer;
    scale: 1.2;
    background-color: rgb(187, 95, 95);

}


.gameGrid .game {

    border: 3px solid black;


    width: 6rem;
    padding-block: 20px;




}

.gameGrid .game p {

    font-size: 1.1rem;
    margin: auto;


}




.spanType2 {

    color: rgb(255, 255, 255);
    font-size: 1.2rem;

}

.lastGame {
    margin-block: 30px;
}

.lastGame ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.lastGame img {
    width: 50px;
}

.lastGame span {

    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 25px;
}

.lastGame div {
    display: flex;
}



.rolls {
    display: flex;
}

p {
    margin: 0;
    padding: 0;
}

input {
    margin-bottom: 1rem;
}